<template>
  <div class="home">
    <div class="list-contain">
      <div 
        class="list-item" 
        v-for="(item, index) in contentList"
        @click="handlerDetail(item)"
      >        
        <div class="item-pic"></div>
        <div class="item-text">
          <div class="item-title">{{ item.title }}</div>
          <div class="item-time">{{ item.time }}</div>
        </div>
      </div>
    </div>
    <div class="img-box">
      <img id="img" src="~@/assets/images/birds.jpeg"/>
      <div
        class="draw-box" 
        v-for="(item, index) in drawList" :key="index"
        :style="{
          left: `${item.draw_l}px`, 
          top: `${item.draw_t}px`, 
          width: `${item.draw_w}px`, 
          height: `${item.draw_h}px`
        }"
      >         
        <div class="draw-class">{{ item.class }}</div>
        <div class="draw-score">{{ (item.score*100).toFixed(2) }}%</div>
      </div>
    </div>
    <div class="img-box">
      <img id="img2" src="~@/assets/images/beach.jpg"/>
      <div
        class="draw-box" 
        v-for="(item, index) in drawList2" :key="index"
        :style="{
          left: `${item.draw_l}px`, 
          top: `${item.draw_t}px`, 
          width: `${item.draw_w}px`, 
          height: `${item.draw_h}px`
        }"
      >
        <div class="draw-class">{{ item.class }}</div>
        <div class="draw-score">{{ (item.score*100).toFixed(2) }}%</div>
      </div>
    </div>   
  </div>
</template>

<script>

export default {
  data() {
    return {
      contentList: [
        {
          title: 'aaa',
          time: '2024-01-01'
        },
        {
          title: 'bbb',
          time: '2024-01-01'
        },
        {
          title: 'ccc',
          time: '2024-01-01'
        }
      ],
      drawList: [],
      drawList2: []
    }
  },
  mounted() {
    const img = document.getElementById('img');
    const img2 = document.getElementById('img2');

    // Load the model.
    cocoSsd.load().then(model => {
      // detect objects in the image.
      model.detect(img).then(predictions => {
        this.drawList = this.detectImage(predictions)
      });
    });

    cocoSsd.load().then(model => {
      // detect objects in the image.
      model.detect(img2).then(predictions => {
        this.drawList2 = this.detectImage(predictions)
      });
    });
  },
  methods: {
    handlerDetail(item) {
      // console.log('测试item', item)
      this.$router.push({
        name: 'itemDetail',
        query: {
          title: item.title,
          time: item.time
        }
      })
    },
    detectImage(predictions) {
      let tempArr = []

      for(let i = 0; i < predictions.length; i++) {
        let obj = {}

        obj.draw_l = predictions[i].bbox[0]
        obj.draw_t = predictions[i].bbox[1]
        obj.draw_w = predictions[i].bbox[2]
        obj.draw_h = predictions[i].bbox[3]
        obj.class = predictions[i].class
        obj.score = predictions[i].score

        tempArr.push(obj)
      }

      return tempArr
    }
  }
}
</script>

